import React, {Component} from "react";
import ItemDescription from "./ItemDescription";

export default class Items extends Component {

    state = {
        selectedItem: null
    };

    constructor(props) {
        super(props);

        this.selectItem = this.selectItem.bind(this);
    }

    selectItem(item) {
        if (!item.isCreating) {
            this.setState({
                selectedItem: item
            })
        }
    }

    render() {
        return (
            <div className='items column frame'>
                <div className='rpgui-container framed'>

                    <div className={'row'}>
                        <strong>Items:</strong>
                        <hr/>
                    </div>

                    {
                        this.state.selectedItem ?

                            <div className='rpgui-container framed-grey item'>
                                {
                                    <ItemDescription item={this.state.selectedItem}/>
                                }
                            </div> : null
                    }

                    <div className='item-list'>
                        {
                            this.props.items.length > 0 ?
                                this.props.items.map((item, index) => {
                                    return (
                                        <span key={index}
                                              className={`rpgui-icon sword ${item.isCreating ? 'rpgui-disabled' : ''}`}
                                              onClick={() => this.selectItem(item)}>

                                            {
                                                !item.isCreating ?
                                                    <span className='item-level'>{item.level}</span> : null
                                            }

                                            {
                                                item.isCreating ?
                                                    <span className='item-timeout'>
                                                        {item.getCreationTimeout()}
                                                    </span> : null
                                            }

                                        </span>
                                    )
                                }) : <div className='row'>go Crft!</div>
                        }
                    </div>

                </div>
            </div>
        )
    }
}